header {
  height: 100vh;
  padding-top: 7rem;
  overflow: hidden;
}

.header__container {
  text-align: center;
  height: 100%;
  position: relative;
}

.perfil {
  height: 140%;
  width: 350%;
  margin-left: -15rem;
  padding-bottom: 15rem;
}

/* ---------- CTA ---------- */
.cta {
  margin-top: 2.5rem;
  display: flex;
  gap: 1.2rem;
  justify-content: center;
}

/* ---------- Header Socials ---------- */
.header__socials {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 0.8rem;
  position: absolute;
  left: 0;
  bottom: 3rem;
}

.header__socials::after {
  content: "";
  width: 1px;
  height: 2rem;
  background: var(--color-primary);
}

/* ---------- ME ---------- */
.me {
  background: linear-gradient(var(--color-primary), transparent);
  width: 22rem;
  height: 30rem;
  position: absolute;
  left: calc(50% - 11rem);
  margin-top: 2rem;
  border-radius: 12rem 12rem 0 0;
  overflow: hidden;
  padding: 1rem 1rem 0rem 0rem;
  box-shadow: 0 0px 9px 0.2px #fff;
}

/* ---------- SCROLL DOWN ---------- */
.scroll__down {
  position: absolute;
  right: -2.3rem;
  bottom: 5rem;
  transform: rotate(90deg);
  font-weight: 300;
  font-size: 0.9rem;
}

/* ---------- MEDIA QUERIES (TABLET) ---------- */

@media screen and (max-width: 1024px) {
  header {
    height: 100vh;
  }

  .me {
    padding: 1rem 1rem 0rem 0rem;
  }

  .perfil {
    height: 140%;
    width: 350%;
    margin-left: -14rem;
    padding-bottom: 10rem;
  }
}

/* ---------- MEDIA QUERIES (PHONE) ---------- */

@media screen and (max-width: 600px) {
  header {
    height: 120vh;
  }
  .header__socials,
  .scroll__down {
    display: none;
  }

  .me {
    padding: 1rem 1rem 0rem 0rem;
  }
}
